*, *:before, *:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Nunito', sans-serif;
  font-size: 1rem;;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media (prefers-reduced-motion: no-preference) {
  .blob-spin {
    -webkit-animation: blob-spin infinite 60s linear;
            animation: blob-spin infinite 60s linear;
    -webkit-animation-delay: 5s;
            animation-delay: 5s;
  }

  .fadeIn {
    opacity: 0;
    -webkit-animation: fadeIn 1s ease-in;
            animation: fadeIn 1s ease-in;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
  }

  .fadeDelay {
    -webkit-animation: fadeDelay 0.8s ease-out;
            animation: fadeDelay 0.8s ease-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
    opacity: 0;
  }
}

@-webkit-keyframes blob-spin {
  0% {
    -webkit-transform: scale(1) rotate(0deg);
            transform: scale(1) rotate(0deg);
  }
  30% {
    -webkit-transform: scale(.8) rotate(15deg);
            transform: scale(.8) rotate(15deg);
  }
  60% {
    -webkit-transform: scale(.9) rotate(-15deg);
            transform: scale(.9) rotate(-15deg);
  }
  100% {
    -webkit-transform: scale(1) rotate(0deg);
            transform: scale(1) rotate(0deg);
  }
}

@keyframes blob-spin {
  0% {
    -webkit-transform: scale(1) rotate(0deg);
            transform: scale(1) rotate(0deg);
  }
  30% {
    -webkit-transform: scale(.8) rotate(15deg);
            transform: scale(.8) rotate(15deg);
  }
  60% {
    -webkit-transform: scale(.9) rotate(-15deg);
            transform: scale(.9) rotate(-15deg);
  }
  100% {
    -webkit-transform: scale(1) rotate(0deg);
            transform: scale(1) rotate(0deg);
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeDelay {
  0% {
    opacity: 0;
    -webkit-transform: translate(0, 3rem);
            transform: translate(0, 3rem);
  }

  100% {
    opacity: 1;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}

@keyframes fadeDelay {
  0% {
    opacity: 0;
    -webkit-transform: translate(0, 3rem);
            transform: translate(0, 3rem);
  }

  100% {
    opacity: 1;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}

.app {
  overflow: hidden;
}

a, a:visited {
  color: #007AFF;
  text-decoration: none;;
}

a:hover {
  color: #FF47C5;
  text-decoration: none;
}


/* my styles */
.header {
  font-size: 1.125rem;
  padding-top: 2.5rem;
  padding-bottom: 5rem;
  text-align: center;
}

.me {
  height: 48px;
}

.intro {
  margin: 0.5rem 0;
}

.role {
  display: block;
}

.personal-statement {
  font-family: 'Merriweather', serif;
  font-size: 1.75rem;
  font-weight: 300;
  line-height: 1.571;
  margin: 0 auto;
  max-width: 980px;
  padding: 0 1.25rem;
  text-align: center;
}

/* About me */
.blob {
  height: 1180px;
  position: absolute;
  top: -10rem;
  left: -38rem;
  z-index: -1;
}

.about-me {
  -ms-flex-line-pack: stretch;
      align-content: stretch;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 1.125rem;
  font-weight: 600;
  line-height: 1.523;
  margin: 6rem auto;
  min-height: 866px;
  max-width: 1220px;
  overflow: visible;
  padding: 0 1.25rem;
